.card {
  border: 1px solid $white-gray-color;
  width: 166px;
  border-radius: 5px;
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: transform, $transition-duration;

  &__image {
    height: 134px;
    margin-bottom: 40px;
    align-self: center;
  }

  &__name {
    width: 166px;
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $primary-color;
    transition: color, $transition-duration;

    &:hover {
      color: $title-color;
    }
  }

  &__code {
    margin-bottom: 16px;
    font-size: 10px;
    line-height: 14px;
    color: $secondary-color;
  }

  &__reviews {
    text-align: right;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $primary-color;
  }

  &__price {
    width: 166px;
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-text {
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
      color: $secondary-color;
    }

    &-number {
      text-align: right;
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
      color: $primary-color;
    }
  }

  &__button-link {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__button {
    border: 0;
    color: white;
    background-color: $button-color;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    height: 40px;
    width: 166px;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
    transition-property: background-color, color, border;
    transition-duration: $transition-duration;

    &:hover {
      background-color: $white-color;
      color: $button-color;
      border: 1px solid $button-color;

      cursor: pointer;
    }
  }

  &:hover {
    transform: scale(1.2);
  }
}
