body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

a {
  color: #000;
  text-decoration: none;
}

.h2, .h4 {
  margin: 0;
}

button {
  padding: 0;
}

p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.container--logo {
  display: flex;
}

.container--nav {
  height: 100%;
  position: relative;
}

.list {
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

.list__item {
  align-items: center;
  height: 60px;
  margin: 0 10px;
  list-style: none;
  display: flex;
}

.list__item:first-child {
  margin-left: 0;
}

.list__item:last-child {
  margin-right: 0;
}

.list__link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
  transition: all .3s;
}

.list__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  align-items: center;
  height: 100%;
  display: flex;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 166px;
  padding: 32px 16px 16px;
  transition: transform, all .3s;
  display: flex;
}

.card__image {
  align-self: center;
  height: 134px;
  margin-bottom: 40px;
}

.card__name {
  color: #060b35;
  width: 166px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color, all .3s;
}

.card__name:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-link {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 0;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  transition-property: background-color, color, border;
  transition-duration: .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card__stars {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-bottom: 24px;
  display: flex;
}

.card__stars-4 {
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.card__stars-star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.card__stars-star:first-child {
  margin: 0;
}

.card__stars-star:last-child {
  background-image: url("star.822d561e.svg");
}

.cards {
  grid-template-columns: 200px 200px 200px 200px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 1px) {
  .cards {
    grid-template-columns: 200px;
  }
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: 200px 200px;
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: 200px 200px 200px;
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: 200px 200px 200px 200px;
  }
}

